import React from 'react'
import rehypeReact from "rehype-react";
import { CodeBlock } from './CodeBlock';
import { IconContext } from 'react-icons/lib/cjs';
import { Icon } from './Icon';
import * as moment from 'moment';
import 'moment/locale/de';

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: { 
    "code-block": CodeBlock,
    "icon": Icon,
  }
}).Compiler

moment.locale("de")

export class HTMLContent extends React.Component<{className?: string, content: string}>
{
  render()
  {
    return (
      <div className={this.props.className} dangerouslySetInnerHTML={{__html: this.props.content}}>
      </div>
    )
  }
}

export class ReactHTMLContent extends React.Component<{className?: string, html: string}>
{
 
  render()
  {
    return (
      <IconContext.Provider value={{ className: 'react-icons' }}>
        <div className={this.props.className}>
          {renderAst(this.props.html)}
        </div>
      </IconContext.Provider>
    )
  }
}

export class Content extends React.Component<{className?: string, content: React.ReactNode}>
{
  render()
  {
    return(
      <div className={this.props.className}>{this.props.content}</div>
    )
  }
}


