import React from "react";
import "./SideBar.sass";
import { Link } from "gatsby";

export class SideBarHelp extends React.Component<{}>
{
  render()
  {
    return (
      <div className="sidebar-component">
        <div className="sidebar-header">
            Benötigen Sie Hilfe?
        </div>
        <div className="sidebar-content">
          <div className="sidebar-content-pad">
            <div style={{marginBottom: "5px"}}>
              <Link to="/trainings">Informieren Sie sich über unsere Trainings!</Link>
            </div>
          </div>
          <div style={{textAlign: "center", marginTop: "15px"}}>
            <Link to="/kontakt" className="button action-button">Kontakt aufnehmen</Link>
          </div>
        </div>
      </div>
    )
  }
}