

import React from "react";
import "./SideBar.sass";
import { graphql, StaticQuery, Link } from 'gatsby'
import { nestCategories, constructCategoryUrl, INestedCategory } from '../../tool';


class SideBarCategories extends React.Component<{categories: INestedCategory[]}>
{
  render()
  {
    let categories = nestCategories(this.props.categories);
    return (
      <div className="sidebar-component">
        <div className="sidebar-header">
            Kategorien
        </div>
        <div className="sidebar-content sidebar-content-pad">
          <ul>
            {
              categories.map((c,i) => {
                return (
                  <li key={i}>
                    <Link to={constructCategoryUrl(c)}>{c.friendlyname}</Link>
                    <ul>
                      {
                        (c.subcategories || [] as INestedCategory[]).map((c,i) => (
                          <li key={i}>
                            <Link to={constructCategoryUrl(c)}>{c.friendlyname}</Link>
                            <ul>
                              {
                                (c.subcategories || [] as INestedCategory[]).map((c,i) => (
                                    <li key={i}>
                                      <Link to={constructCategoryUrl(c)}>{c.friendlyname}</Link>
                                    </li>
                                ))
                              }
                            </ul>
                          </li>
                        ))
                      }
                    </ul>
                  </li>
                )
              })
              // nested.map((c,i)) => {
              //   <li key={i}>
              //     <Link to={c.route}>{c.text}</Link>
              //   </li>
              // }
            }
          </ul>
        </div>
      </div>
    )
  }
}


export default () => (
  <StaticQuery
    query={graphql`
    {
      ...categories
    }
    `}
    render={(data) => {
      return (<SideBarCategories categories={data.categories.edges.map(e => e.node)}/>)
    }}
  />
)
