import * as React from "react";
import { ICategory, IAuthor, ICustomerStory, ITag } from "./interfaces";
import { SideBarContent } from "./components/SideBar/SideBarContent";
import { SideBarQuickstart } from "./components/SideBar/SideBarQuickstart";
import { SideBarHelp } from "./components/SideBar/SideBarHelp";
import SideBarCategories from "./components/SideBar/SideBarCategories";
import { PostFooter } from "./components/ContentFooter/PostFooter";
import { AuthorFooter } from "./components/ContentFooter/AuthorFooter";
import { ContactFooter } from './components/ContentFooter/ContactFooter';
import { CustomerStoryFooter } from './components/ContentFooter/CustomerStoryFooter';
import { SubscriberFooter } from "./components/ContentFooter/SubscriberFooter";
import { SubscribeFooter } from "./components/ContentFooter/SubscribeFooter";

export function randint(min, max)
{
  return Math.floor(Math.random() * (max - min)) + min;
}

export function getAuthor(slug, authors: IAuthor[]): IAuthor
{
  let res = authors.find(a => a.frontmatter.slug == slug);
  if(res == null)
    throw new Error(`Could not find author for slug ${slug}`);
  else 
    return res;
}

export function constructCategoryUrl(category: INestedCategory)
{
  let res = category.slug;
  let current = category;
  while(current.parent != null)
  {
    current = current.parent;
    res = current.slug + "/" + res;
  }
  return "category/" + res;
}

export function getCategory(categoryslug: string, categories: INestedCategory[]): INestedCategory
{
  function visit(category: INestedCategory): INestedCategory
  {
    if(category.slug == categoryslug)
    {
      return category;
    }
    else 
    {
      if(category.subcategories != null)
      {
        for(let c of category.subcategories)
        {
          let res = visit(c);
          if(res != null)
            return res;
        }
        return null;
      }
      else
      {
        return null;
      }
    }
  }
  // let nested = nestCategories(categories);
  for(let c of categories)
  {
    let res = visit(c);
    if(res != null)
      return res;
  }
  throw new Error(`Could not find category for slug ${categoryslug}`);
}

export type INestedCategory = ICategory & { parent: INestedCategory, subcategories: INestedCategory[] }

export function nestCategories(categories: ICategory[]): INestedCategory[]
{
  let cat = JSON.parse(JSON.stringify(categories))
  function visit(category: ICategory, parent = null) : INestedCategory
  {
    if(category.subcategories != null)
    {
      (category.subcategories as any) = category.subcategories.map(c => visit(c, category));
    }
    (category as INestedCategory).parent = parent;
    return category as INestedCategory;
  }

  return cat.map(c => visit(c));
}

export function urlJoin(a: string, b: string)
{
  if(a[a.length-1] == "/")
    a = a.substring(0, a.length-1);
  if(b[0] == "/")
    b = b.substring(1);
  return a + "/" + b;
}

export function mapSidebarComponent(slug: string, page: {tableOfContents: string, frontmatter: {categories: string[]}})
{
  switch(slug)
  {
    case "content": 
      return (
        <SideBarContent tableOfContents={page.tableOfContents}/>
      )
    case "quickstart":
      return (
        <SideBarQuickstart/>
      )
    case "help": 
      return (
        <SideBarHelp/>
      )
    case "categories":
      return (
        <SideBarCategories/>
      )
    default:
      throw new Error(`Could not map sidebar component with slug ${slug}`)
  }
}

export function mapFooterComponent(slug: string, page: {frontmatter: {author: string; categories: string[]; tags: string[]}}, categories: INestedCategory[], author: IAuthor, customerStories: ICustomerStory[], avatar: any, tags: ITag[])
{
  switch(slug)
  {
    case "postfooter": 
      return (
        <PostFooter tags={page.frontmatter.tags.map(t => tags.find(tag => tag.slug == t))} categories={categories}/>
      )
    case "author":
      return (
        <AuthorFooter author={author} avatar={avatar}/>
      )
    case "contact":
      return (
        <ContactFooter/>
      )
    case "customerstory":
      return (
        <CustomerStoryFooter stories={customerStories}/>
      )
    case "subscribe": 
      return (
        <SubscribeFooter></SubscribeFooter>
      )
    default:
      throw new Error(`Could not map footer component with slug ${slug}`)
  }
}
