import React from "react";
import Helmet from "react-helmet";
import { SubscriptionService } from "../../services/SubscriptionService";
import "./Newsletter.sass";

export class NewsLetter extends React.Component<{}, { subscribed: boolean, showState: boolean }> {
  constructor(props) {
    super(props);
    this.state = {
      subscribed: false,
      showState: false
    };
  }

  componentWillMount()
  {
    this.setState({
      subscribed: SubscriptionService.subscribed
    });
  }

  handler = (state: boolean) => {
    this.setState({
      subscribed: state,
      showState: true
    });
    setTimeout(() => {
      this.setState({
        showState: false
      })
    }, 2000);
    
  };

  onClick = () => {
    SubscriptionService.openForm();
  };

  componentWillUnmount() {
    SubscriptionService.unregisterSubscriptionHandler(this.handler);
  }

  componentDidMount() {
    SubscriptionService.registerSubscriptionHandler(this.handler);
  }

  render() {
    return (
      <div>
        {
          this.state.showState == true ? (
            <div className="state-container">
              <div className="state-div vert-center">
                <div className="vert-center">
                  {
                    SubscriptionService.state == "old" ? 
                    <span>Sie sind schon zu unserem Newsletter angemeldet. Vielen Dank!</span> 
                    : 
                    <span>Vielen Dank für die Anmeldung an unserem Newsletter! <br/>In kürze wirst du eine Bestätigungsemail erhalten, in der Sie weitere Instruktionen finden.</span>
                  }
                </div>
              </div>

            </div>
          ): ""
        }
        <div>
          {
            this.state.subscribed == false ? (<div className="newsletter-container">
              <div style={{margin: "auto"}}>
                <a onClick={this.onClick}>
                  Abboniere unseren Newsletter und erhalte den Udemy-Kurs "Einführung in
                  Power Pivot" gratis
                </a>
              </div>
          </div>) : ""
          }
        </div>
      </div>
    );
  }
}
