import React from "react";
import { FaSmileWink, FaSmile, FaSmileBeam, FaGrin, FaRegGrinAlt } from "react-icons/fa";

export class Icon extends React.Component<{id: string}>
{
  components = { 
    "smile": FaRegGrinAlt,
    "smile-wink": FaSmileWink,
    "smile-beam": FaSmileBeam,
    "grin": FaGrin,
  }
  render() {
    let ctor = this.components[this.props.id];
    if(ctor == null)
      console.error(`Could not find icon with id {id}`)

    return (
      <span className="post-icon">
        {new ctor()}
      </span>
    )
  }
}