import React from "react";
import { Link } from "gatsby";
import { constructCategoryUrl, INestedCategory } from '../../tool';
import "./ContentFooter.sass";
import { ITag } from "../../interfaces";

interface IPostFooterProps
{
  tags: ITag[];
  categories: INestedCategory[];
}

export class PostFooter extends React.Component<IPostFooterProps>
{
  render()
  {
    return (
      <div className="contentfooter-component">
        <div>
          <span style={{fontWeight: "bold"}}>Schlagworte: </span>
          {
            this.props.tags.map((c,i) => (
              <span key={i}>
                <Link to={"tag/"+c.slug}>{c.name}</Link>
                {
                  i < this.props.tags.length - 1 ? <span> - </span> : ""
                }
              </span>
            ))
          }
        </div>
        <div>
        <span style={{fontWeight: "bold"}}>Kategorien: </span>
          {
            this.props.categories.map((c,i) => (
              <span key={i}>
                <Link to={constructCategoryUrl(c)}>{c.friendlyname}</Link>
                {
                  i < this.props.categories.length - 1 ? <span> - </span> : ""
                }
              </span>
            ))
          }
        </div>
        <hr/>
      </div>
    )
  }
}