import { getCookieObject } from "../misc/shared";

class SubscriptionServiceClass
{
  private handlers: ((state: boolean) => any)[] = [];
  public subscribed: boolean = false;
  public state: "old"|"new" = null;

  constructor() 
  {
    if(typeof window !== "undefined") //This prevents SSR from accessing document which is undefined during node build
    {
      let cookieObj = getCookieObject();
      this.subscribed = cookieObj.subscribed == "true";

      if(document.getElementById("n2g_script") == null)
      {
        let script = `
        ! function(e, t, n, c, r, a, i) {
          e.Newsletter2GoTrackingObject = r, e[r] = e[r] || function() {
              (e[r].q = e[r].q || []).push(arguments)
          }, e[r].l = 1 * new Date, a = t.createElement(n), i = t.getElementsByTagName(n)[0], a.async = 1, a.src = c, i.parentNode.insertBefore(a, i)
        }(window, document, "script", "https://static.newsletter2go.com/utils.js", "n2g");
        var config = {
          "container": {
              "type": "div",
              "class": "",
              "style": "",
              "id": "n2gForm"
          },
          "row": {
              "type": "div",
              "class": "",
              "style": "margin-top: 15px;"
          },
          "columnLeft": {
              "type": "div",
              "class": "",
              "style": ""
          },
          "columnRight": {
              "type": "div",
              "class": "",
              "style": ""
          },
          "label": {
              "type": "label",
              "class": "",
              "style": "display:inline;"
          },
                     "input": {
              "class": "",
              "style": "width:100%;padding: 5px 10px; border-radius: 2px; border: 1px solid #d8dee4;"
          },
                     "checkbox": {
             "type": "input",
              "class": "",
              "style": "margin:0px 5px 0px 0px"
          },
                     "captchaImage": {
                     "type": "img",
                     "class": "",
                     "style": "padding: 5px; border: 0px solid #fff; margin-top: 5px; width: 32px; height: 32px; cursor: pointer; display:inline;",
                     "style:active": "padding: 5px; border: 3px solid #ccc; margin-top: 5px; width: 32px; height: 32px; cursor: pointer;"
          },
                     "button": {
              "type": "button",
              "class": "",
              "id": "asd",
              "style": "background-color: #9CA31E; border: none; border-radius: 4px; padding: 10px 20px; color: #ffffff; margin-top: 20px; cursor: pointer;"
          },
                     "message": {
              "type": "h2",
              "class": "",
              "id": "",
              "style": "text-align: center;color:#9CA31E;"
          },
        };
        n2g('create', '5ghc6ehf-5cjsjypj-69s');
        `
        const el = document.createElement("script");
        el.id = "n2g_script"
        el.innerHTML = script;
        document.body.appendChild(el);
      }
    }
  }

  openForm()
  {
    (window as any).n2g("subscribe:createPopup", (window as any).config, 0, this.onSubscribe);
  }

  closeForm()
  {
    (document.getElementById("n2gForm").getElementsByTagName("a")[0] as HTMLAnchorElement).click();
  }

  onSubscribe = (ev) => {
    if(ev.status == 201)
    {
      this.state = "new";
    }
    else if(ev.status == 200)
    {
      this.state = "old";
    }
    this.setSubscribed();
    this.closeForm();
  }

  showSuccess()
  {
  }

  registerSubscriptionHandler(handler: (state: boolean) => any)
  {
    this.handlers.push(handler);
  }

  unregisterSubscriptionHandler(handler: (state: boolean) => any)
  {
    let index = this.handlers.indexOf(handler);
    if(index >= 0)
      this.handlers.splice(index, 1);
  }

  setSubscribed()
  {
    this.subscribed = true;
    this.handlers.forEach(h => h(this.subscribed));
    document.cookie = "subscribed=true";
  }
}

export const SubscriptionService = new SubscriptionServiceClass();
