import React from "react";
import { ICustomerStory } from '../../interfaces';
import { CustomerStory } from "../CustomerStory/CustomerStory";
import { randint } from "../../tool";

export class CustomerStoryFooter extends React.Component<{stories: ICustomerStory[]}>
{
  render() 
  {
    let idx = randint(0, this.props.stories.length);
    return (
      <div className="contentfooter-component">
        <hr/>
        <div>
          <h4> Kundenmeinungen </h4>
        </div>
        <CustomerStory story={this.props.stories[idx]}/>
      </div>
    )
  }
}