import React from "react";
import "./SideBar.sass";
import { Link } from "gatsby";

export class SideBarQuickstart extends React.Component<{}>
{
  render()
  {
    return (
      <div className="sidebar-component">
        <div className="sidebar-header">
            Quickstart
        </div>
        <div className="sidebar-content">
          <ul>
            <li>
              <Link to="/quickstart-eine-erste-auswertung-mit-power-pivot">Das Quickstart Tutorial</Link>
            </li>
            <li>
              <Link to="/was-ist-power-pivot">Was ist Power Pivot?</Link>
            </li>
            <li>
              <Link to="/was-ist-power-bi">Was ist Power BI?</Link>
            </li>
            <li>
              <Link to="/was-ist-power-query">Was ist Power Query?</Link>
            </li>
            <li>
              <Link to="/power-pivot-power-bi-oder-power-query">Power Query, Power BI oder Power Pivot?</Link>
            </li>
            <li style={{ marginTop: "20px"}}>
              <Link to="/beispiel-datenbanken">Download Beispieldaten</Link>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}