import React from "react";
import { IAuthor } from "../../interfaces";
import Img from "gatsby-image"
import { HTMLContent } from '../Content/Content';
import "./ContentFooter.sass"
import { AuthorContent } from "../Author/AuthorContent";

export class AuthorFooter extends React.Component<{author: IAuthor, avatar: any}>
{
  render()
  {
    return (
      <div className="contentfooter-component author-footer">
        <AuthorContent author={this.props.author} avatar={this.props.avatar}/>
      </div>
    )
  }
}
