import React from "react";
import { Link } from "gatsby";

export class ContactFooter extends React.Component
{
  render() 
  {
    return (
      <div style={{textAlign: "center"}} className="contentfooter-component">
        <Link className="button action-button" to="/kontakt">Kontakt aufnehmen</Link>
      </div>
    )
  }
}