import React from "react";
import { Link, StaticQuery, graphql } from "gatsby";
import github from "../../../data/img/github-icon.svg";
import logo from "../../../data/img/logo_w.png";
import "./Navbar.sass"
import Img from 'gatsby-image';
import { FaTwitter, FaFacebook, FaPhone } from "react-icons/fa";
import { OutboundLink } from "gatsby-plugin-google-analytics";

interface NavbarProps {
  logo: any
}

interface NavbarState {
  active: boolean;
  navBarActiveClass: string;
}

export class Navbar extends React.Component<NavbarProps, NavbarState> {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
      navBarActiveClass: ""
    };
  }

  toggleHamburger = () => {
    // toggle the active boolean in the state
    this.setState(
      {
        active: !this.state.active
      },
      // after state has been updated,
      () => {
        // set the class in state for the navbar accordingly
        this.state.active
          ? this.setState({
              navBarActiveClass: "is-active"
            })
          : this.setState({
              navBarActiveClass: ""
            });
      }
    );
  };

  render() {
    let navbarItems = [
      { text: "Home", route: "/"},
      { text: "Trainings", route: "/trainings"},
      { text: "Sofort-Service", route: "/sofort-service"},
      { text: "Power BI", route: "/category/power-bi"},
      { text: "Power Pivot", route: "/category/power-pivot"},
      { text: "Power Query", route: "/category/power-query"},
      { text: "Über uns", route: "/ueber-uns"},
    ]
    return (
      <nav className="navbar is-transparent" role="navigation" aria-label="main-navigation">
        <div className="container">
          <div className="navbar-brand">
            <Link to="/" title="Logo">
              <Img fixed={this.props.logo.childImageSharp.fixed}></Img>
            </Link>
            {/* Hamburger menu */}
            <div className={`navbar-burger burger ${this.state.navBarActiveClass}`} data-target="navMenu" onClick={() => this.toggleHamburger()}>
              <span />
              <span />
              <span />
            </div>
          </div>
          <div id="navMenu" className={`navbar-menu ${this.state.navBarActiveClass}`}>
            <div className="navbar-start has-text-centered">
            {
              navbarItems.map((n,i) => {
                return (
                  <Link key={i} className="navbar-item" activeClassName="navbar-active" to={n.route}>
                    <span>{n.text}</span>
                  </Link>
                )
              })
            }
              <Link className="navbar-item" activeClassName="navbar-active" to="/kontakt">
              <FaPhone style={{marginRight: "5px"}} className="primary-col"/>
              <span>Kontakt</span>
              </Link>
            </div>
            <div className="navbar-end has-text-centered">
              <OutboundLink className="navbar-item social" aria-label="Twitter@ppvinsights" href="https://twitter.com/ppvinsights" rel="noopener nofollow" target="_blank">
                <FaTwitter/>
              </OutboundLink>
              <OutboundLink className="navbar-item" aria-label="Facebook" href="https://www.facebook.com/powerpivotinsights" rel="noopener nofollow" target="_blank">
                <FaFacebook/>
              </OutboundLink>
            </div>
          </div>
        </div>
      </nav>
    );
  }
};


export default () => (
  <StaticQuery
    query={graphql`
    {
      logo: file(
        absolutePath: { regex: "/.*\/data\/img\/*/"}  
        relativePath: { eq: "logo_w.png" }
        ) {
        childImageSharp {
          fixed(height: 50) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
    `}
    render={(data) => <Navbar logo={data.logo}/>}
  />
)


