import React from "react";
import { SubscriptionService } from "../../services/SubscriptionService";

export class SubscribeFooter extends React.Component
{
  onClick = () => {
    SubscriptionService.openForm();
  }

  render() 
  {
    return (
      <div style={{textAlign: "center"}} className="contentfooter-component">
        <a className="button action-button" onClick={this.onClick}>Zum Newsletter anmelden</a>
      </div>
    )
  }
}