import React from "react";
import "./Footer.sass";
import Img from 'gatsby-image';
import { StaticQuery, graphql, Link } from "gatsby";

class Footer extends React.Component<{icon: any}>
{
  render() 
  {
    return (
      <div className="footer-container">
        <div className="footer-link-container">
          <span>
            <Link to="/impressum">Impressum</Link>
          </span>
          <span>
            <Link to="/haftungsausschluss">Haftungsausschluss</Link>
          </span>
          <span>
            <Link to="/datenschutz">Datenschutz</Link>
          </span>
        </div>
        <div className="footer-icon">
          <Img fixed={this.props.icon.childImageSharp.fixed}></Img>
        </div>
      </div>
    )
  }
}


export default () => (
  <StaticQuery
    query={graphql`
    {
      icon: file(absolutePath: {regex: "/.*/data/img/footer\\-icon/"}) {
        childImageSharp {
          fixed(width: 142) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
    `}
    render={data => {
      return (
      <Footer icon={data.icon}>
      </Footer>
    )}
    }
  />
)