import React from "react";
import "./SideBar.sass";

interface ISideBarContentProps {
  tableOfContents: string;
}
export class SideBarContent extends React.Component<ISideBarContentProps>
{
  render()
  {
    //table of contents generated links like /test/#anchor but this will refresh the page and is not desired!
    let tableOfContentsHtml = this.props.tableOfContents.replace(/href="\/.*\/(#.*?)"/g, (s, a) => {
      return "href=\"" + a + "\"";
    }).replace(/<\/?p>/g, "")

    return (
      <div className="sidebar-component">
        <div className="sidebar-header">
            Inhalt
        </div>
        <div id="toc-sidebar" className="sidebar-content">
          <div dangerouslySetInnerHTML={{__html: tableOfContentsHtml}}>
          </div>
        </div>
      </div>
    )
  }

  onScroll = (ev: Event) => {
    let offset = window.scrollY;
    let els    = Array.from(document.getElementsByClassName("anchor"))
    if(els.length == 0)
      return;
    els = els.map(d => d.parentElement)

    let currentActive = Array.from(document.getElementsByClassName("sidebar-contentitem-active"));
    if(currentActive.length >= 0)
      currentActive.forEach(c => c.className = "");

    let diffs    = els.map((h: HTMLElement) => Math.abs(offset - h.offsetTop))
    let minIndex = diffs.indexOf(Math.min(...diffs));
    let id       = (els[minIndex].firstChild as HTMLHRElement).getAttribute("href").substr(1);

    let target = Array.from(document.getElementById("toc-sidebar").getElementsByTagName("a")).find(e => {
      return e.getAttribute("href").indexOf(id) >= 0
    });
    if(target)
      target.parentElement.className = "sidebar-contentitem-active";
  }

  componentDidMount()
  {
    window.addEventListener("scroll", this.onScroll);
    let els = Array.from(document.getElementsByClassName("anchor")).map(d => d.parentElement);
    if(els.length != 0)
    {
      let id     = els[0].getAttribute("id");
      let target = Array.from(document.getElementById("toc-sidebar").getElementsByTagName("a")).find(e => {
        return e.getAttribute("href").indexOf(id) >= 0
      });
      if(target)
        target.parentElement.className = "sidebar-contentitem-active";
    }
  }

  componentWillUnmount()
  {
    window.removeEventListener("scroll", this.onScroll);
  }
}

